<template>
  <div class="schedule">
    <div class="schedule-item item" v-for="item in tableData" :key="item.id">
      <div class="flowVin detail">{{ item.detail[0] }}</div>
      <div class="flowId detail">{{ item.detail[1] }}</div>
      <div class="createDate detail">创建时间：{{ moment(item.creationDate).format('YYYY-MM-DD HH:mm:ss')}}</div>
      <router-link v-if="item.flowOp === '信息查询'" :to="{name: 'pretrialChecker', params: {id: item.id, objId: item.objId, url: item.url } }" class="flowOp">{{ item.flowOp }}</router-link>
      <router-link v-if="item.flowOp === '选号'" :to="{name: 'platePick', params: {id: item.id, objId: item.objId, url: item.url } }" class="flowOp">{{ item.flowOp }}</router-link>
      <router-link v-if="item.flowOp === '邮寄号牌'" :to="{name: 'plateMail', params: {id: item.id, objId: item.objId, url: item.url } }" class="flowOp">{{ item.flowOp }}</router-link>
      <router-link v-if="item.flowOp === '开票'" :to="{name: 'faceInfo', params: {id: item.id, objId: item.objId, url: item.url } }" class="flowOp">{{ item.flowOp }}</router-link>
      <router-link v-if="item.flowOp === '验车'" :to="{name: 'vechicleChecker', params: {id: item.id, objId: item.objId, url: item.url } }" class="flowOp">{{ item.flowOp }}</router-link>
    </div>
    <div class="empty" v-if="tableData.length === 0">
      <img :src="emptyUrl" />
      <div class="text">加油少年，数据为空哦~</div>
    </div>
  </div>
</template>

<script>
import { getSearchFlow } from "@/api/user";
import moment from 'moment'
export default {
  name: 'Schedule',
  data() {
    return {
      tableData: [],
      emptyUrl: require('../assets/empty.png')
    }
  },
  mounted() {
    getSearchFlow().then(resp => {
        this.tableData = this.tableData.concat(resp.data.content)
    })
  },
  methods: {
    moment: moment
  },
}
</script>

<style lang='scss' scoped>
  .schedule{
      min-width: 420px;
      margin: 0 auto;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      text-align: left;
      max-width: 1024px;
    
    .item {
        position: relative;
        margin: 18px 0;
        padding: 16px 18px;
        box-shadow: 0 3px 3px #e6e4e4;
        border-radius: 5px;
        .detail {
          margin: 6px 0;
          font-size: 14px;
        }
      .flowOp {
        color: #2f8ade;
        position: absolute;
        top: 8px;
        right: 12px;
        display: block;
        padding: 8px 12px;
        -webkit-box-shadow: 0 1px 6px #ccc;
        box-shadow: 0 1px 6px #ccc;
        border-radius: 4px;
        font-size: 14px;
      }
    }
  }
  .text {
    font-size: 14px;
  }
  .box-card {
    width: 480px;
    margin: 0 auto;
  }

  .empty {
      text-align: center;
      margin-top: 42px;
      .text{
        color: #bbb;
        font-size: 14px;
      }
    }
</style>
